<template>
  <div>
    <h1 style="text-align: center">TodoList</h1>
    <el-progress :text-inside="true" :stroke-width="26" :percentage="done"></el-progress>
    <br>
    <el-button type="primary" @click="dialogFormVisible = true">添加</el-button>
<!--    <el-button  v-else type="warning" @click="resetProcess">重置</el-button>-->
    <el-popconfirm
        placement="bottom"
        title="确定重置进度吗"
        width="200"
        trigger="click"
                   style="margin-left: 10px;"
        @confirm="resetProcess">
      <el-button v-if="done===0" disabled type="warning" slot="reference">重置</el-button>
      <el-button v-else type="warning" slot="reference" >重置</el-button>
    </el-popconfirm>
    <el-dialog title="添加" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="事件">
          <el-input v-model="new_todo_info.todo"></el-input>
        </el-form-item>
        <el-form-item label="权重">
          <el-input v-model.number="new_todo_info.percent"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="add_todo">确 定</el-button>
      </div>
    </el-dialog>


    <item v-for="(t,index) of todos"
          :key="index"
          :id="t.id"
          :todo="t.todo"
          :percent="t.percent"
          @deleteTodo="deleteTodo"
          @complete_todo="complete_todo"></item>
  </div>
</template>

<script>
import item from "@/components/todo_components/Item";
import {nanoid} from "nanoid";

export default {
  name: "todo",
  data() {
    return {
      todos: [],
      done: 0,
      dialogFormVisible: false,
      new_todo_info: {
        todo: '',
        percent: '',
      }
    }
  },
  methods: {
    complete_todo(percent) {
      this.done = this.done + parseFloat(percent);
      // this.done = this.done + percent;

    },
    add_todo() {
      this.todos.push({
        id: nanoid(),
        todo: this.new_todo_info.todo,
        percent: this.new_todo_info.percent
      })
      this.dialogFormVisible = false;
      this.new_todo_info.percent = '';
      this.new_todo_info.todo = '';
    },
    deleteTodo(key) {
      this.todos = this.todos.filter((a) => {
        return a.id != key
      })
    },
    resetProcess() {
      this.done = 0;
    },

  }
  ,
  components: {
    item
  },
  mounted() {
    let t = JSON.parse(localStorage.getItem("todos"));
    if (t !== null) {
      this.todos = t;
    } else {
      this.todos = [];
    }

    let tp = localStorage.getItem('done_percent');
    if (tp !== null) {
      this.done = parseFloat(tp);
    } else {
      this.done = 0;
      localStorage.setItem("done_percent", JSON.stringify(this.done));

    }
  },
  updated() {
    localStorage.setItem("todos", JSON.stringify(this.todos))
    if (JSON.stringify(this.done) !== null) {
      localStorage.setItem("done_percent", JSON.stringify(this.done));
    }

  },


}
</script>

<style scoped>

</style>